<template>
	<view class="order-list-wrap">
		<!-- tab 栏标题 -->
		<view>
			<u-tabs-swiper
			ref="tabs" 
			:list="list" 
			:is-scroll="false"
			font-size="26"
			:current="current"
			@change="tabsChange"
			></u-tabs-swiper>
		</view>
		<!-- tab 内容 -->
		<view style="height: 100%;">
			<swiper style="height: 100%;" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
				<swiper-item class="swiper-item" v-for="(item, index) in swiperList" :key="index">
					<scroll-view scroll-y @scrolltolower="reachBottom" class="scroll-view">
						<view class="order-tab-content">
							<template v-if="isEmpty">
								<empty-state></empty-state>
							</template>
							<template v-else>
								{{index}}
								<order-item :orderList='item'></order-item>
								<u-loadmore :status="loadStatus[index]" :load-text="loadText"></u-loadmore>
							</template>
						</view>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	import orderItem from '@/components/order-item/order-item.vue'
	import emptyState from '@/components/empty-state/empty-state.vue'
	import { getOrderListMixin } from '@/common/mixin.js'
	
	export default {
		mixins: [ getOrderListMixin ],
		components: {
			orderItem,
			emptyState
		},
		props: {
			isOnShow: Number
		},
		data() {
			return {
				list: [], // tab 标题栏内容
				current: 0, // tabs 组件的 current 值，表示当前活动的 tab 选项
				swiperCurrent: 0, // swiper 组件的 current 值，表示当前那个 swiper-item 是活动的
				dx: 0,
				// 加载更多
				loadStatus: ['loadmore', 'loadmore', 'loadmore'],
				loadText: {
					loadmore: this.$t('commonText.loadmore'),
					loading: this.$t('commonText.loading'),
					nomore: this.$t('commonText.noMore')
				},
				// 是否显示空状态
				isEmpty: false,
				// swiper 列表
				swiperList: [[], [], []],
				// 运单数据列表
				orderList: [],
				// 请求参数
				queryParams: {
					PageIndex: 1,
					PageSize: 10,
					StartTime: '',
					EndTime: '',
					LogisticsStatus: 8700
				},
				// 是否有更多数据
				hasMore: true
			}
		},
		methods: {
			// tab 切换请求数据相关操作
			async onTabsChange(index) {
				this.swiperCurrent = index;
				// 重置一些参数
				this.orderList = []
				this.queryParams.PageIndex = 1
				this.hasMore = true
				this.isEmpty = false
				// tab 显示
				switch (index){
					case 0:
						this.queryParams.LogisticsStatus = 8700
						break;
					case 1:
						this.queryParams.LogisticsStatus = 99999
						break;
					case 2:
						this.queryParams.LogisticsStatus = 12200
						break;
					default:
						this.queryParams.LogisticsStatus = 8700
						break;
				}
				await this.getOrderList()
			},
			// 触底
			reachBottom() {
				
			},
			// tab栏切换
			tabsChange(index) {
				this.onTabsChange(index)
			},
			// swiper-item 的位置发生改变时会触发
			transition({ detail: { dx } }) {
				this.$refs.tabs.setDx(dx)
				// 重置状态
				// this.current = 0
				// this.orderList = []
				// this.queryParams.LogisticsStatus = 8700
				// this.queryParams.PageIndex = 1
				// this.hasMore = true
				// this.isEmpty = false
			},
			// 动画结束时触发
			animationfinish({ detail: { current } }) {
				this.$refs.tabs.setFinishCurrent(current)
				this.swiperCurrent = current
				this.current = current
				this.onTabsChange(current)
			},
			// 获取 tab 栏各项的数量，并赋值
			async getTabItemNum() {
				const tabItemNum = await this.$request({
					method: 'POST',
					url: 'deliveryorder/statistics'
				})
				const { abnormal, deliverying, unsettled } = tabItemNum.data
				this.list = [
					{ name: this.$t('order.ordering') + ' (' + deliverying + ')' },
					{ name: this.$t('order.abnormalOrder') + ' (' + abnormal + ')' },
					{ name: this.$t('order.settlement') + ' (' + unsettled + ')' }
				]
			}
		},
		watch: {
			async isOnShow() {
				// 获取 tab 各个状态的数量
				this.getTabItemNum()
				// 重置状态
				this.current = 0
				this.swiperList = [[], [], []]
				this.orderList = []
				this.queryParams.LogisticsStatus = 8700
				this.queryParams.PageIndex = 1
				this.hasMore = true
				this.isEmpty = false
				
				// 获取列表数据
				// 判断是否直接显示异常订单
				const isAbnormal = getApp().globalData.isAbnormal
				if (isAbnormal) {
					this.onTabsChange(1)
					this.queryParams.LogisticsStatus = 99999
					await this.getOrderList()
					this.swiperList.splice(1, 1, this.orderList)
					console.log(this.swiperList)
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.order-list-wrap{
		height: calc(100vh - 160rpx);
		.swiper-item{
			height: 100%;
			.scroll-view{
				height: calc(100vh - 240rpx);
				.order-tab-content{
					padding: 20rpx 30rpx;
				}
			}
		}
	}
</style>
